import $ from 'jquery';
import { TimelineMax, Expo, Power4 } from 'gsap';


export class Loader {
    constructor() {
        this.time = 2;
        this.count = 0;
        this.animateLoad()
    }
        
    animateLoad() {
        $('body').addClass('is-animating');
        this.count =0;
        this.timer = setInterval(() => {
            $('.percents').html(`${this.count}%`)
            this.count++;
            if(this.count === 101) {
                clearInterval(this.timer);
                this.animateChange();
            }
        }, this.time*10);
    }

    animateChange() {
        
        let tl = new TimelineMax({onComplete: () => {
            $('.loader').css('display', 'none')
            $('.pagination').css('transform', 'translateY(-50%)')
            $('.social-media').css('transform', 'translateY(-50%)')
        }});
        tl
            .set($('#app'), {scale: 0, opacity: 1})
            .set($('.pagination'),{x:"-30vh"})
            .set($('.menu-icon'),{x:"30vh"})
            .set($('.logo'),{x:"-30vh"})
            .set($('.social-media'), {x:"30vh"})
            .set($('.title-item'), {y: "4vw"})
            .set($('.header-content__subtitle'), {x: "4vw", opacity: 0})
            .set($('.header-content__line'), {x: "4vw", opacity: 0})
            .set($('.header-content__link-block'), {x: "4vw", opacity: 0})
            .to($('.percents'), 0.3, {scale: 0, opacity: 0})
            .to($('#app'), 1.5, {scale: 1, ease: Power4.easeInOut}, '0.3')
            .to([$('.logo'), $('.pagination')], 2.5, {x: 0, ease: Expo.easeOut},'1.4')
            .to([$('.menu-icon'), $('.social-media')], 2.5, {x: 0, ease: Expo.easeOut}, '1.4') 
            .staggerTo($('.title-item'), 1.5, { y: 0, ease: Expo.easeOut}, '0.1', '1.3')
            .staggerTo([$('.header-content__subtitle'), $('.header-content__line'), $('.header-content__link-block')], 3, {
                x: 0,
                opacity: 1,
                ease: Expo.easeOut
            }, '0.2', '1.6')
            .set($('body'), {className: "-=is-animating"})
            
    }



    

    
}