import { TweenLite, TweenMax, TimelineMax, Power4 } from 'gsap';
export class MenuIcon {
    constructor() {
        this.elem = $('#icon')
        this.animateIcon()
    }
    animateIcon() {
        let tl2 = new TimelineMax({paused: true});
        let tl = new TimelineMax({paused: true});
        
        tl
            .to($('.nav-mobile'), 1.3, {x: "-100%", ease: Power4.easeInOut}, '0')
            .staggerTo($('.nav-mobile__item'), 1.8, {x: 0, ease: Power4.easeOut}, '0.04', '0')

        tl.reverse();
        
        tl2
            .to($('.menu-icon__item_top'), 0.2, {y: 10, },'0' )
            .to($('.menu-icon__item_bottom'), 0.2, {y: -10,},'0')
            .to($('.menu-icon__item_center'), 0.1, {opacity: 0})
            .to($('.menu-icon__item_top'), 0.1, {rotation: 45 , y: '0.6vw'}, '0.3')
            .to($('.menu-icon__item_bottom'), 0.1, {rotation: -45, y: '-0.6vw'}, '0.3')
            .to($('.menu-icon__item_top'), 0.8, {rotation: "-=720" }, '0.4' )
            .to($('.menu-icon__item_bottom'), 0.8, {rotation: "-=720"}, '0.4')

        tl2.reverse();
        
        let move = (e) => {
            $('.menu-icon__item').toggleClass('menu-active');
            tl.reversed(!tl2.reversed());
            tl2.reversed(!tl2.reversed());
        }

        this.elem.on('click', move)
    }
}

