import normalizeWheel from 'normalize-wheel';
import { TweenLite, Expo, Power4, TimelineMax } from 'gsap';

export class ScrollAction {
    constructor() {
        this.currentY = 0;
        this.moveSections();
        this.dataSlide = 1;
        this.dotY = 0;
    }

    moveSections() {
        let animateMove = (e) => {
            if(!$('body').hasClass('is-animating')) {
                $('body').addClass('is-animating');
                let norm = normalizeWheel(e);
                if (norm.spinY > 0) {
                    this.currentY += 100;
                    this.dataSlide += 1;
                    this.dotY += 2.55;
                } else {
                    this.currentY -= 100;
                    this.dataSlide -= 1;
                    this.dotY -= 2.55;
                }
                
                if (this.currentY < 0) {
                    this.currentY = 0;
                    this.dataSlide = 1;
                    this.dotY = 0;
                }

                if(this.currentY > 400) {
                    this.currentY = 400;
                    this.dataSlide = 5;
                    this.dotY = 10.2;
                }
                console.log(this.currentY);
                TweenLite.to($('#main-layout'),0.9, {y: `-${this.currentY}vh`, ease: Expo.easeInOut, onUpdate: () => {
                    
                    if(this.dataSlide === 2 || this.dataSlide === 4 ){
                        $('.pagination-number').css('color','#333333');
                        $('.menu-icon__item').css('background-color','#333333');
                        $('.dot-active').css('background-color','#333333');
                       
                    } else {
                        $('.pagination-number').css('color','white');
                        $('.menu-icon__item').css('background-color','white');
                        $('.dot-active').css('background-color','white');
                    }
                },
                    onComplete: () => {
                        $('body').removeClass('is-animating');
                        this.animateContent();
                    }
                })

                TweenLite.to($('.dot-active'), 0.9, {y: `${this.dotY}vw`})
            }
            
        }
        document.addEventListener('mousewheel', animateMove)
        document.addEventListener('DOMMouseScroll', animateMove)
    }

    animateContent() {
        $('.pagination-number').html(`0${this.dataSlide}`);
        
        switch(this.dataSlide) {
            case 2: {
                let tl2 = new TimelineMax();
                tl2
                    .staggerTo($('.studio-title__item'), 1.3,{y:0, ease: Power4.easeOut, opacity: 1}, 0.05)
                    .staggerTo($('.studio-image__overlay'), 1.6, {scaleX: 0, ease: Power4.easeOut},"0.3","0.4")
                    .staggerTo($('.studio-description__item'), 2, {y: 0 , ease: Power4.easeOut, opacity: 1}, '0.15', '0.3')
                break;
            }
            case 3: {
                let tl3 = new TimelineMax();
                tl3
                    .staggerTo($('.services-block__txt-item'), 1.3, {y:0, ease: Power4.easeOut, opacity: 1}, 0.05)
                    .to($('.services-block__line'), 0.7, {scaleX: 1, ease: Power4.easeInOut},"0.6" )
                    .staggerTo($('.services-block__item'), 0.5, {y: 0, opacity: 1, ease: Power4.easeOut},'0.2', '0.6')
                    .to($('.item-block__title')[0], 1.2, {y:0, ease: Power4.easeOut}, '0.3')
                    .to($('.item-block__description')[0], 2, {y:0, ease: Power4.easeOut}, '0.2', '0.4')
                break;
            }
            case 4: {
                let tl4 = new TimelineMax();
                tl4
                    .staggerTo($('.blog__title-item'), 1.3, {y: 0, ease: Power4.easeOut}, 0.05)
                    .to($('.blog__line'), 0.7, {scaleX: 1, ease: Power4.easeInOut}, '0.7')
                    .to($('.blog__image-overlay'), 1, {scaleX: 0, ease: Power4.easeOut}, '0.7')
                    .to($('.blog-article__line'), 0.7, {scaleX: 1, ease: Power4.easeInOut},'1')
                    .to($('.blog-article__item'), 1, {y:0 , ease: Power4.easeOut},'1.4')
                    .to($('.blog-article__description'), 1, {y:0, opacity: 1, ease: Power4.easeOut},'1.6')
                    .to($('.blog-item__title'), 1, {y: 0, ease: Power4.easeOut}, '1.8')
                    .to($('.blog-item__description'), 1, {y: 0, ease: Power4.easeOut, opacity:1 }, '2')
                break;
                }
            case 5: {
                let tl5 = new TimelineMax();
                tl5
                    .staggerTo($('.contacts-title__item'), 1.3, {y: 0, ease: Power4.easeOut}, 0.05)
                    .to($('.contacts-line'), 0.5, {scaleX: 1, ease: Power4.easeInOut}, '0.5')
                    .staggerTo($('.contacts-item__title'), 1, {y: 0, ease: Power4.easeOut}, '0.1', '1')
                    .staggerTo($('.contacts-item__label'), 1, {y: 0, ease: Power4.easeOut}, '0.1', '1.2')
                    .to($('.map-overlay'), 1.5, {scaleX: 0, ease: Power4.easeOut}, '0.5')
                    .to($('.icon'), 0.8, {opacity: 1},'1.7')
                break;
            }    
        }
    }
}